import React, {useState} from 'react';
import {Row, Col, Divider, Radio, Grid} from 'antd';
import s from './../Dashboard/Dashboard.module.css';
import {compose} from "redux";
import {withRouter} from "react-router";
import {connect} from "react-redux";
import {designConfirmationStatus} from "../../../redux/contentReducer";



const {useBreakpoint} = Grid;

const style = {
    /*background: '#0092ff',*/
    padding: '8px 0'
};


const SingleRoomPage = (props) => {
    console.log(props)

    /*Сохряняем цифру из URL, для фильтрации по ней, чтобы определить какую комнату показать*/
    let showedRoomId = props.match.params.roomId


    const screens = useBreakpoint();

    let roomToShow = props.room.filter(function(currentValue, index, arr) {
       return currentValue.id === +showedRoomId;
     });


/*    let architectureToShow = props.architecture.filter(function(currentValue, index, arr) {
        return currentValue.id === +showedRoomId;
    });
    console.log(roomToShow)
    console.log(roomToShow[0])*/



    let [mainImage, setMainImage] = useState(roomToShow[0].images[0]);

    let changeShowedImage = (url) => {
        setMainImage({
            image: url
        });
    };

    let smallImages = roomToShow[0].images.map(key => {
        return <div className={s.smallImage} onClick={() =>{changeShowedImage(key)} }>
            <img src={key} alt={"interior design"}/>
        </div>
    })

    /*let smallArchitectureImages = architectureToShow[0].images.map(key => {
        return <div className={s.smallImage} onClick={() =>{changeShowedImage(key)} }>
            <img src={key} alt={"Architecture design"}/>
        </div>
    })*/



    const options = [
        { label: 'Waiting for approval', value: null },
        { label: 'Reject', value: false },
        { label: 'Confirm', value: true },
    ];


    const DESIGN_CONFIRMATION = 'DESIGN_CONFIRMATION';
    const actions = {
        designConfirmationAC: (designConfirmation, designID) => ({
            type: DESIGN_CONFIRMATION,
            confirmedDesign: designConfirmation,
            designID: designID
        }),
    }


    let onChange3 = (e) => {
            /*e => {*/
        /*console.log('radio3 checked', e.target.value);*/
/*        this.setState({
            value3: e.target.value,
        });*/
        console.log(e.target.value)
        let confirmationStatus = e.target.value
        /*props.designConfirmationStatus(confirmationStatus, showedRoomId)*/
        props.dispatch(actions.designConfirmationAC(confirmationStatus, showedRoomId))
    };

    let selectedConfirmationOption = 1

    const value3= "Confirm";

    return (
        <>
            <div>
                <div style={{margin: '10px 10px 10px 0', float: 'left'}}>
                    <Radio.Group
                        options={options}
                        onChange={onChange3}
                        value={props.room[showedRoomId].confirmedDesign}
                        optionType="button"
                    />
                </div>
                <div>
                    <img src={mainImage.image ? mainImage.image : roomToShow[0].images[0] } alt={"interior design"}/>
                </div>
                { smallImages }
            </div>
        </>

    )
}



const mapStateToProps = (state) => ({

})


const mapDispatchToProps = (state) => ({
    designConfirmationStatus: designConfirmationStatus
})

export default compose(
    withRouter,
    connect(mapStateToProps, mapDispatchToProps))(SingleRoomPage);