import React from 'react';
import {Redirect, Route, Switch} from "react-router-dom";
import {withRouter} from "react-router";
import './App.css';
import {Avatar, Layout, Menu, Breadcrumb, Col, Grid} from "antd";
import HeaderBar from "./components/Header/Header";
import Navbar from "./components/Navbar/Navbar";
import Dashboard from "./components/content/Dashboard/Dashboard";
import {compose} from "redux";
import {connect} from "react-redux";
import InteriorDesign from "./components/content/InteriorDesign/InteriorDesgin";
import Architecture from "./components/content/Architecture/Architecture";
import SingleRoomPage from "./components/content/SingleRoomPage/SingleRoomPage";
import LoginPage from "./components/content/Login/login";

const {Header, Footer, Sider, Content} = Layout;
const {SubMenu} = Menu;

function App(props) {

    return (
        <div className="App">
            <Layout>
                <HeaderBar/>

                <Layout>
                    <Navbar/>
                    <Layout>
                        <Content style={{padding: '0 50px'}}>
                            <Breadcrumb style={{margin: '16px 0'}}>
                                <Breadcrumb.Item>Dashboard</Breadcrumb.Item>
                            </Breadcrumb>

                            <Route path='/dashboard'
                                   render={() => {
                                       return <Dashboard
                                           interiorDesign={props.state.contentPage.interiorDesign}
                                           architectureDesign={props.state.contentPage.architectureDesign}
                                       />
                                   }}/>


                            <Switch> {/*<Switch> нужен, чтобы если используешь несколько Route с идентичными частями пути, то отрисовывалось первое совпадение. Например, можно сделать общую страницк логинизации на path='/login' и специальную path='/login/facebook/, без применения Switch отрисуются сразу обе компоненты, а если применить Switch, то нужно прописать path='/login/facebook/ выше, тогда react отрисует именно эту компоненту и остановится (не будет искать другие совпадения url) */}

                                <Route
                                    path='/interiorDesign/:roomId' /*синтексис :roomId, сообщет роутеру, что тут должен быть какой-то параметр, который мы для нашего понимания кода обозначили как roomId. Значек ? означает, что параметр roomId является опциональным, его может не быть */
                                    render={() => {
                                        return <SingleRoomPage
                                            room={props.state.contentPage.interiorDesign}
                                            dispatch={props.dispatch}
                                        />
                                    }}/>

                                <Route
                                    path='/architecture/:roomId' /*синтексис :roomId, сообщет роутеру, что тут должен быть какой-то параметр, который мы для нашего понимания кода обозначили как roomId. Значек ? означает, что параметр roomId является опциональным, его может не быть */
                                    render={() => {
                                        return <SingleRoomPage room={props.state.contentPage.architectureDesign}/>
                                    }}/>

                                <Route path='/architecture'
                                       render={() => {
                                           return <Architecture/>
                                       }}/>

                                <Route path='/interior_design'
                                       render={() => {
                                           return <InteriorDesign
                                               interiorDesign={props.state.contentPage.interiorDesign}
                                           />
                                       }}/>
                            </Switch>

                            <Route path='/login'
                                   render={() => {
                                       return <LoginPage />
                                   }}/>

                        </Content>
                        <Footer style={{textAlign: 'center'}}>VIP Client Account | Spazio Interior Decoration
                            LLC</Footer>
                    </Layout>
                </Layout>
            </Layout>


        </div>
    );
}


const mapStateToProps = (state) => ({
    state: state
})


const mapDispatchToProps = (state) => ({})

export default compose(
    withRouter,
    connect(mapStateToProps, mapDispatchToProps))(App);