export const contentReducer = (state = initialState, action) => {

    switch (action.type) {
        case DESIGN_CONFIRMATION: {
            return {
                ...state,
                interiorDesign: state.interiorDesign.map(u => {
                    if (u.id === +action.designID) {
                        return {...u, confirmedDesign: action.confirmedDesign}
                    }
                    return u
                })

            };
        }

        default:
            return state;
    }
}


export const actions = {
    designConfirmationAC: (designConfirmation, designID) => ({
        type: DESIGN_CONFIRMATION,
        confirmedDesign: designConfirmation,
        designID: designID
    }),
}


export const designConfirmationStatus = (confirmationStatus, designID) => (dispatch) => {
    dispatch(actions.designConfirmationAC(confirmationStatus, designID)); /*после завешения получения инициализацийонных данных с сервера мы dispatch в state инфу, что инициализация завершилась */
    console.log(confirmationStatus)
    console.log(designID)
}


export default contentReducer;