import React, {useState} from 'react';
import {Row, Col, Divider, Grid} from 'antd';
import s from './../Dashboard/Dashboard.module.css';
import SingleArchitectureBlock from "../Dashboard/SingleBlock/SingleArchitectureBlock";
import {compose} from "redux";
import {withRouter} from "react-router";
import {connect} from "react-redux";
import {NavLink} from "react-router-dom";


const {useBreakpoint} = Grid;

const style = {
    /*background: '#0092ff',*/
    padding: '8px 0'
};


const Architecture = (props) => {

    const screens = useBreakpoint();

    let architectureBlocks = props.architectureDesign.map(key => {
        return <Col key={key.id} className="gutter-row" span={screens.lg || screens.md ? 6 : 24}>
            <SingleArchitectureBlock key={key.id} room={key} windowSize={screens.lg || screens.md ? 6 : 24}/>
        </Col>
    })

    return (
        <>
            <Divider orientation="left">
                <NavLink to="/architecture">Architecture Design</NavLink>
            </Divider>
            <Row gutter={{xs: 8, sm: 16, md: 24, lg: 32}}>
                {architectureBlocks}
            </Row>
        </>

    )
}


const mapStateToProps = (state) => ({
    architectureDesign: state.contentPage.architectureDesign
})


const mapDispatchToProps = (state) => ({})

export default compose(
    withRouter,
    connect(mapStateToProps, mapDispatchToProps))(Architecture);