import React, {useState} from 'react';
import {Row, Col, Divider, Grid} from 'antd';
import s from './Dashboard.module.css';
import SingleRoomBlock from "./SingleBlock/SingleRoomBlock";
import InteriorDesign from "./../InteriorDesign/InteriorDesgin";
import {compose} from "redux";
import {withRouter} from "react-router";
import {connect} from "react-redux";
import SingleArchitectureBlock from "./SingleBlock/SingleArchitectureBlock";
import Architecture from "../Architecture/Architecture";

const {useBreakpoint} = Grid;

const style = {
    /*background: '#0092ff',*/
    padding: '8px 0'
};




const Dashboard = (props) => {



    const screens = useBreakpoint();
    /*let ObjectKeys = Object.keys(props.interiorDesign)*/


    let interiorBlocks = props.interiorDesign.map(key => {
        return <Col key={key.id} className="gutter-row" span={screens.lg || screens.md ? 6 : 24}>
            <SingleRoomBlock key={key.id} room={key} windowSize={screens.lg || screens.md ? 6 : 24}/>
        </Col>
    })

    let architectureBlocks = props.architectureDesign.map(key => {
        return <Col key={key.id} className="gutter-row" span={screens.lg || screens.md ? 6 : 24}>
            <SingleArchitectureBlock key={key.id} room={key} windowSize={screens.lg || screens.md ? 6 : 24}/>

        </Col>
    })


    return (
        <>
            <InteriorDesign />
            <Architecture />

           {/* <Divider orientation="left">Interior Design</Divider>
            <Row gutter={{xs: 8, sm: 16, md: 24, lg: 32}}>
                {interiorBlocks}
                                <Col className="gutter-row" span={6}>
                    <SingleRoomBlock room={props.interiorDesign.livingRoom}/>
                </Col>
                <Col className="gutter-row" span={6}>
                    <SingleRoomBlock room={props.interiorDesign.masterBedroom}/>
                </Col>
                <Col className="gutter-row" span={6}>
                    <div style={style}>
                        <img className={s.gridImages} src={props.interiorDesign.bedroom1.images[0]}/>
                    </div>
                    <div>
                        <div><b>{props.interiorDesign.bedroom1.name.toUpperCase()}</b></div>
                        <span>{props.interiorDesign.bedroom1.floor}</span>
                    </div>
                </Col>
                <Col className="gutter-row" span={6}>

                </Col>
            </Row>*/}
           {/* <Divider orientation="left">Interior Design</Divider>
            <Row gutter={{xs: 8, sm: 16, md: 24, lg: 32}}>
                {interiorBlocks}

            </Row>
            <Divider orientation="left">Architecture Design</Divider>
            <Row gutter={{xs: 8, sm: 16, md: 24, lg: 32}}>
                {architectureBlocks}

            </Row>*/}
        </>

    )
}


/*export default Dashboard;*/

const mapStateToProps = (state) => ({
    interiorDesign: state.contentPage.interiorDesign,
    architectureDesign: state.contentPage.architectureDesign
})



const mapDispatchToProps = (state) => ({

})

export default compose(
    withRouter,
    connect(mapStateToProps, mapDispatchToProps))(Dashboard);