import React, {useState} from 'react';
import {Row, Col, Divider, Grid, Menu} from 'antd';
import s from './../Dashboard/Dashboard.module.css';
import SingleRoomBlock from "./../Dashboard/SingleBlock/SingleRoomBlock";
import {compose} from "redux";
import {withRouter} from "react-router";
import {connect} from "react-redux";
import {NavLink} from "react-router-dom";


const {useBreakpoint} = Grid;

const style = {
    /*background: '#0092ff',*/
    padding: '8px 0'
};


const InteriorDesign = (props) => {

    const screens = useBreakpoint();

    let interiorBlocks = props.interiorDesign.map(key => {
        return <Col key={key.id} className="gutter-row" span={screens.lg || screens.md ? 6 : 24}>
            <SingleRoomBlock key={key.id} room={key} windowSize={screens.lg || screens.md ? 6 : 24}/>
        </Col>
    })

    return (
        <>
            <Divider orientation="left">
                <NavLink to="/interior_design">Interior Design</NavLink>
            </Divider>
            <Row gutter={{xs: 8, sm: 16, md: 24, lg: 32}}>
                {interiorBlocks}
            </Row>
        </>

    )
}


const mapStateToProps = (state) => ({
    interiorDesign: state.contentPage.interiorDesign
})


const mapDispatchToProps = (state) => ({})

export default compose(
    withRouter,
    connect(mapStateToProps, mapDispatchToProps))(InteriorDesign);