import React, {useState} from 'react';
import {Row, Col, Divider} from 'antd';
import s from './../Dashboard.module.css';
import {NavLink} from "react-router-dom";

const style = {
    /*background: '#0092ff',*/
    padding: '8px 0'
};


const SingleRoomBlock = (props) => {

    return (
       <>
           <div style={style}>

               <NavLink to={'/interiorDesign' + `/` + props.room.id}>
                   <img style={{width: '100%'}} src={props.room.images[0]}/>
               </NavLink>

           </div>
           <div>
               <NavLink to={'/interiorDesign' + `/` + props.room.id}>
                   <div><b>{props.room.name.toUpperCase()}</b></div>
               </NavLink>

               <span className={s.floorText}>{props.room.floor}</span>
           </div>
       </>
)
}


export default SingleRoomBlock;


