import React, {useState} from 'react';
import {NavLink} from "react-router-dom";

import {Button, Layout, Menu} from "antd";
import {
    CodepenOutlined,
    EditOutlined,
    FileDoneOutlined,
    HomeOutlined,
    MenuFoldOutlined,
    MenuUnfoldOutlined,
    PieChartOutlined
} from '@ant-design/icons';

const {Sider} = Layout;
const {SubMenu} = Menu;


const Navbar = (props) => {

    let [editMode, setEditMode] = useState(false);

    let toggleCollapsed = () => {
        setEditMode({
            collapsed: !editMode.collapsed,
        });
        console.log(editMode.collapsed)
    };

    return (
        <Sider collapsed={editMode.collapsed}>
            <div style={{margin:'40px 0 0 0'}}>
                <Button type="primary" onClick={toggleCollapsed} style={{marginBottom: 16}}>
                    {React.createElement(editMode.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
                </Button>
                <Menu
                    defaultSelectedKeys={['Dashboard']}
                    mode="inline"
                    theme="dark"
                    inlineCollapsed={editMode.collapsed}

                >
                    <Menu.Item key='Dashboard' icon={<PieChartOutlined/>}>
                        <NavLink to="/dashboard">Dashboard</NavLink>
                    </Menu.Item>

                    <Menu.Item key='interior_design' icon={<CodepenOutlined/>}>
                        <NavLink to="/interior_design">Interior Design</NavLink>
                    </Menu.Item>

                    <Menu.Item key='architecture' icon={<HomeOutlined/>}>
                        <NavLink to="/architecture">Architecture</NavLink>
                    </Menu.Item>

                   <Menu.Item key='drawings' icon={<EditOutlined/>}>
                       <NavLink to="/drawings">Drawings</NavLink>
                    </Menu.Item>

                   <Menu.Item key='documents' icon={<FileDoneOutlined/>}>
                       <NavLink to="/documents">Documents</NavLink>
                    </Menu.Item>

                   {/* <SubMenu
                        key="sub1"
                        title={
                            <span>
                                <CodepenOutlined/>
                                <NavLink to="/interior_design">Interior Design</NavLink>
                            </span>
                        }>
                        <Menu.ItemGroup key="g1" title="Ground Floor">
                            <Menu.Item key="1">Men's majlis</Menu.Item>
                            <Menu.Item key="2">Women's majlis</Menu.Item>
                            <Menu.Item key="3">Formal Dining</Menu.Item>
                            <Menu.Item key="4">Family Living room</Menu.Item>
                            <Menu.Item key="4">Entrance</Menu.Item>
                        </Menu.ItemGroup>
                        <Menu.ItemGroup key="g2" title="First Floor">
                            <Menu.Item key="1">Master bedroom</Menu.Item>
                            <Menu.Item key="2">Family living</Menu.Item>
                        </Menu.ItemGroup>
                    </SubMenu>
                    <SubMenu
                        key="sub2"
                        title={
                            <span>
                                <HomeOutlined/>
                                <NavLink to="/architecture">Architecture</NavLink>
                            </span>
                        }>
                        <Menu.Item key="1">Facades</Menu.Item>
                        <Menu.Item key="2">Landscape</Menu.Item>
                    </SubMenu>
                    <SubMenu
                        key="sub3"
                        title={
                            <span>
                                        <EditOutlined/>
                                        <NavLink to="/drawings">Drawings</NavLink>
                                    </span>
                        }>
                        <Menu.Item key="1">Architecture</Menu.Item>
                        <Menu.Item key="2">Interior</Menu.Item>
                    </SubMenu>
                    <SubMenu
                        key="sub4"
                        title={
                            <span>
                                <FileDoneOutlined/>
                                <NavLink to="/documents">Documents</NavLink>
                            </span>
                        }>
                        <Menu.Item key="1">Contract</Menu.Item>

                        <Menu.Item key="2">Invoices</Menu.Item>
                    </SubMenu>*/}

                </Menu>
            </div>


        </Sider>
    )
}

export default Navbar;